<template>
  <div class="container">
    <header class="jumbotron">
      <h3>   {{this.$route.params.driverName}}</h3>
         : <b>{{averageRating}}</b>;     <select v-model="rate" class="header_select">
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
        </select>
    </header>
    <body>
    <div class="card" v-for="comment in filteredList">
        <div class="row">
            <div class="col-sm-12">
                <div class="top-buffer font-weight-bold text-center"><h4 class="text-left">{{comment.nameFrom}}</h4></div>
                <div class="row top-buffer">
                    <div class="col-sm-2 light-color"> : </div>
                    <div class="col-sm-3">{{comment.rate}}</div>

                    <div class="col-sm-3 light-color">  : </div>
                    <div class="col-sm-4">{{comment.comment}}</div>

                    <div class="col-sm-3 light-color">{{comment.date}}</div>
                </div>
            </div>
         </div>
         </div>

    </body>
  </div>
</template>

<script>
import CommService from '../../services/comment.service';

export default {
  name: 'list_route',
    data: function () {
        return {
            rate: '',
            averageRating: '2,5',
            comments: []
        };
    },
  computed:{
      filteredList: function(){
            var rt = this.rate;
            return this.comments.filter(function (elem) {
                if(rt=='') return true;
                else return (elem.rate.indexOf(rt) > -1);
            })
        }
      },
  mounted() {
    CommService.getDriverComments(this.$route.params.driverName).then(
      response => {
        this.comments = response.data;
      },
      error => {
        this.comments =
          (error.response && error.response.data) ||
          error.message ||
          error.toString();
      }
    );
      CommService.getDriverRate(this.$route.params.driverName).then(
          response => {
              this.averageRating = response.data;
          },
          error => {
              this.averageRating =
                  (error.response && error.response.data) ||
                  error.message ||
                  error.toString();
          }
      );
  }
};
</script>
<style>

.header_select{
    font-weight: normal;
    color: black;
    background-color: #eaedf0;
    border-color: #eaedf0;
}

</style>
